const base = {
	/**
	 * content-type:
	 * form=>application/x-www-form-urlencoded
	 * json=>application/json
	 * ......
	 */
	config() {
		return {
			//https://base.com
			host: '',
			///login
			url: '',
			data: {},
			header: {
				'content-type': 'application/json'
			},
			//
			method: 'POST',
			//0
			timeout: 0,
			dataType: 'json',
			//Stringkey
			requestTaskKey: '',
			//true datafalse headerstatusCodeerrMsgdata
			concise: false,
			showLoading: true,
			errorMsg: '~'
		}
	},
	getOptions(config) {
		let options = {
			...config
		};
		['host', 'timeout', 'requestTaskKey', 'showLoading', 'errorMsg'].forEach(item => {
			delete options[item];
		})
		return options;
	},
	merge(a, b) {
		return Object.assign({}, a, b);
	},
	mergeConfig(defaultConfig, config, init) {
		let header = base.merge(defaultConfig.header, config.header || {});
		let params = base.merge(defaultConfig, config)
		params.header = header;
		if (!init) {
			let url = base.combineURLs(params.host, params.url)
			params.url = url;
		}
		return params;
	},
	//host
	combineURLs(host, target) {
		return host ? host.replace(/\s+/g, '') + '/' + target.replace(/\s+/g, '').replace(/^\/+/, '') : target;
	},
	toast(text, duration, success) {
		uni.showToast({
			title: text || "~",
			icon: success ? 'success' : 'none',
			duration: duration || 2000
		})
	},
	showLoading(title, mask = true) {
		uni.showLoading({
			mask: mask,
			title: title || '...'
		})
	}
}
export default base
